*,
html,
body {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
body {
  display: flex;
  justify-content: center;
  min-height: 100vh;
  background: rgb(0, 0, 0);
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(31, 31, 31, 1) 10%,
    rgba(65, 65, 65, 1) 20%,
    rgba(90, 91, 90, 1) 31%,
    rgba(121, 121, 121, 1) 40%,
    rgba(129, 129, 129, 1) 50%,
    rgba(112, 157, 135, 1) 73%,
    rgba(88, 154, 122, 1) 85%,
    rgba(44, 138, 94, 1) 98%,
    rgba(19, 108, 66, 1) 100%,
    rgba(0, 99, 54, 1) 100%
  );
}
@media screen and (max-width: 945px) {
  body {
    display: block;
  }
}
a {
  text-decoration: none;
  color: white;
  font-size: 14px;
  font-weight: bold;
}

.App,
#root {
  margin-top: 10px;
  width: 944px;
}

.container {
  display: flex;
  background: rgb(0, 0, 0);
  background: linear-gradient(
    0deg,
    rgba(0, 0, 0, 1) 0%,
    rgba(31, 31, 31, 1) 10%,
    rgba(65, 65, 65, 1) 20%,
    rgba(90, 91, 90, 1) 31%,
    rgba(121, 121, 121, 1) 40%,
    rgba(129, 129, 129, 1) 50%,
    rgba(112, 157, 135, 1) 73%,
    rgba(88, 154, 122, 1) 85%,
    rgba(44, 138, 94, 1) 98%,
    rgba(19, 108, 66, 1) 100%,
    rgba(0, 99, 54, 1) 100%
  );
}
.main-content {
  width: 725px;
  min-height: 500px;
  padding: 30px;
}
h2 {
  margin-bottom: 15px;
  font-size: 18px;
  color: white;
  text-transform: uppercase;
}

p {
  color: white;
  text-align: justify;
  font-size: 16px;
}

.main-container {
  display: flex;
  justify-content: space-between;
}
.main-content div {
  flex-basis: 49%;
}
li {
  font-size: 16px;
}

.box {
  margin-top: 40px;
  width: 100%;
  background-color: #20525c;
  padding: 15px;
}
.referenceList {
  display: flex;
  justify-content: space-between;
}
.img-box {
  display: flex;
}
.img-box img {
  flex-basis: 33%;
  margin: 0 15px;
}
