.sideNav {
  width: 215px;
}
ul {
  list-style-position: inside;
  list-style: none;
  margin: 0 auto;
  color: white;
  width: 100%;
  margin-top: 20px;
}
ul li {
  margin: 10px 0;
  padding-left: 50px;
  cursor: pointer;
}

ul li:before {
  content: "";
  position: absolute;
  border-right: 2px solid white;
  border-bottom: 2px solid white;
  width: 3px;
  height: 3px;
  transform: translateY(-50%) rotate(-45deg);
  margin: 0.6rem 0 0 -1rem;
}

#sub-products li {
  padding-left: 10px;
}
