nav {
  display: flex;
  justify-content: space-between;
}
.logo {
  font-size: 60px;
  font-weight: bold;
  padding: 10px;
}
header {
  background-color: rgb(0, 99, 54);
}
.logo span:first-child {
  color: #00a150;
}
.logo span:last-child {
  color: #ec1b27;
}
.top-nav {
  align-self: flex-end;
  margin-bottom: 4px;
}
.top-nav a {
  margin: 0 10px;
  padding: 0 5px;
  border-bottom: 4px solid #bf212f;
}
.headerImg {
  background-image: url("./index.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
  background-origin: padding-box;
  background-position: 50% 60%;
  background-clip: border-box;
  height: 150px;
}
